/* Navbar *******************************/

.header {
  background-color: #353353;
  border-bottom: 1.5px solid #484663;
}

#nav-icon1 {
  width: 1.8rem;
  height: 1.6rem;
  position: relative;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

#nav-icon1 span {
  display: block;
  position: absolute;
  height: 3px;
  width: 100%;
  border-radius: 1px;
  background: #ffffff;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: 0.2s ease-in-out;
}

#nav-icon1 span:nth-child(1) {
  top: 0px;
}

#nav-icon1 span:nth-child(2) {
  top: 10px;
}

#nav-icon1 span:nth-child(3) {
  top: 20px;
}

#nav-icon1.open span:nth-child(1) {
  top: 10px;
  -webkit-transform: rotate(135deg);
  transform: rotate(135deg);
}

#nav-icon1.open span:nth-child(2) {
  opacity: 0;
  background-color: transparent;
  left: -60px;
}

#nav-icon1.open span:nth-child(3) {
  top: 10px;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
}

.nav-item .active{
  color: gold !important;
}

/* HeroSection *******************************/
.typed-text{
  letter-spacing:2px;
}
.social-icons li a {
  font-size: 1.3rem;
  color: #ffffff;
  box-shadow: 0 5px 20px 0 #d6d6d633;

}
#ic_insta:hover{
  color:#ff133e;
}
#ic_twitter:hover{
  color:#9ac6ff;
}
#ic_in:hover{
  color:#94a8ff;
}
#ic_fi:hover{
  color:rgb(116, 255, 116);
}
#ic_fb:hover{
  color:#9ac6ff;
}
.btn-hero {
  color: #ffffff;
  font-weight: 500;
  border-radius: 25px;
  padding: 10px 32px;
  outline: none;
  border: none;
  background-color: #ff4c60;
}
.btn-hero:hover {
  -webkit-animation: flash 600ms 1 ease;
          animation: flash 600ms 1 ease;
  transition: all 0.2s ease;
}

@-webkit-keyframes flash {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes flash {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.scroll-down {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  z-index: 1;
}

.scroll-wrap {
  text-decoration: none;
  color: #ffffff;
}
.mouse {
  height: 30px;
  width: 24px;
  border: 2px solid #fff;
  border-radius: 16px;
  display: block;
  margin: 10px auto auto;
  position: relative;
}
.wheel {
  width: 5px;
  height: 5px;
  background-color: #ffffff;
  z-index: 1;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  top: 8px;
  border-radius: 50%;
  -webkit-animation: mouse-wheel-ani 2s linear infinite;
          animation: mouse-wheel-ani 2s linear infinite;
}
@-webkit-keyframes mouse-wheel-ani {
  0% {
    top: 4px;
  }
  50% {
    top: 12px;
  }
  100% {
    top: 16px;
  }
}
@keyframes mouse-wheel-ani {
  0% {
    top: 4px;
  }
  50% {
    top: 12px;
  }
  100% {
    top: 16px;
  }
}
.layer {
  display: inline-block;
}
.parallax {
  width: 100%;
  height: 100%;
}
.parallax,
.parallax .layer {
  position: absolute;
  z-index: 1;
}
.parallax .p1 {
  left: 15%;
  top: 15%;
}

.parallax .p2 {
  left: 25%;
  top: 30%;
}

.parallax .p3 {
  left: 15%;
  bottom: 30%;
}

.parallax .p4 {
  left: 10%;
  bottom: 20%;
}

.parallax .p5 {
  left: 20%;
  top: 40%;
}

.parallax .p6 {
  left: 30%;
  bottom: 20%;
}

.parallax .p7 {
  top: 20%;
  right: 30%;
}

.parallax .p8 {
  right: 30%;
  bottom: 20%;
}

.parallax .p9 {
  right: 10%;
  top: 10%;
}

.parallax .p10 {
  top: 45%;
  right: 20%;
}

.parallax .p11 {
  bottom: 24%;
  right: 20%;
}

