/* My Projects */
.project-item{
  /* width: 100%; */
  position: relative;
  overflow:hidden;
  background-color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.project-item .details{
  width: 100%;
    z-index: 99;
    display: -webkit-flex;
